.yesh_slide {
  margin: 20px 0;
  height: 50px;
  border-radius: 25px;
  padding: 0 25px;
  background: #eee;
  border: 1px solid #d1d2d3;
  font-size: 15px;
  color: #929292;
  outline: none;
}

.yesh_slide__btn {
  z-index: 1000;
  position: absolute;
  transform: translate(-50%, -10%);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  border: 1px solid #d1d2d3;
  border-radius: 50%;
  box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.11);
  background: #fff;
}

.yesh_slide__svg {
  width: 24px;
  height: auto;
  pointer-events: none;
}

.yesh_slide__text {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}
